























import { cloneDeep, map } from 'lodash';
import { Component, Prop, Watch, Vue } from 'vue-property-decorator';
import Attribute from '@/components/Attribute.vue';

@Component({
  components: {
    Attribute,
  }
})
export default class ContentDatatable extends Vue {
  @Prop({ default: '' })
  public label!: string;
  @Prop({ default: false })
  public readonly!: boolean;
  @Prop({ default: 1 })
  public header!: number;
  @Prop({ default: false })
  public disabled!: boolean;
  @Prop({ default: () => [] })
  public cols!: Array<object>;
  @Prop({ default: () => [] })
  public value!: Array<object>;

  private newItem = {};
  private data: Array<object> = [];

  @Watch('value')
  onChildChanged2() {
    this.updateData()
  }
  
  // Computed Properties
  private get headers() {
    const headers =  map(this.cols, (i) => {
      return {
        text: i.title,
        value: i.name ? i.name : i.title.toLowerCase(),
        class: `col-${i.type.toLowerCase()}`
      }
    });
    if(!this.readonly) {
      headers.push({
        text: '',
        value: '',
        class: 'col-actions'
      });
    }
    return headers;
  }

  // Methods
  private onInput() {
    console.log(this.data);
    this.$emit('input', this.data);
  }

  private updateData() {
    this.data = cloneDeep(this.value);
  }

  private addRow() {
    console.log(this.newItem);
    this.data.push(this.newItem);
    this.newItem = {};
    this.onInput();
  }

  private created() {
    this.updateData();
  }
};
