



























































import { Component, Prop, Vue } from 'vue-property-decorator';
import AttributeDate from '@/components/AttributeDate.vue';
import AttributeDuration from '@/components/AttributeDuration.vue';
import AttributeName from '@/components/AttributeName.vue';
import AttributeOptions from '@/components/AttributeOptions.vue';
import AttributeNumber from '@/components/AttributeNumber.vue';
import AttributeString from '@/components/AttributeString.vue';
import AttributeUSArmedForces from '@/components/AttributeUSArmedForces.vue';

@Component({
  components: {
    AttributeDate,
    AttributeDuration,
    AttributeName,
    AttributeOptions,
    AttributeNumber,
    AttributeString,
    AttributeUSArmedForces
  },
})
export default class Attribute extends Vue {
  @Prop({ default: '' })
  public type!: string;
  @Prop({ default: '' })
  public label!: string;
  @Prop({ default: false })
  public readonly!: boolean;
  @Prop({ default: false })
  public disabled!: boolean;
  @Prop()
  public value!: any;
  @Prop()
  public data!: any;

  // Computed Properties
  private get template() {
    return this.type.toLowerCase();
  }

  // Methods
  private onInput(newValue: string) {
    this.$emit('input', newValue);
  }
}
